import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';


// for heroku to pull from the backend
let baseUrl = "http://localhost:5000";
if (process.env.NODE_ENV !== "development") {
    baseUrl = "https://exercise-tracker-backend-ja.herokuapp.com";
}


// eslint-disable-next-line
{/* implementation as functional react component */ }
const Exercise = props => (
    <tr>
        <td>{props.exercise.username}</td>
        <td>{props.exercise.description}</td>
        <td>{props.exercise.duration}</td>
        <td>{props.exercise.date.substring(0, 10)}</td>
        <td>
            <Link className="btn btn-primary" to={"/edit/" + props.exercise._id}>edit</Link> <button className="btn btn-secondary" onClick={() => { props.deleteExercise(props.exercise._id) }}>delete</button>
        </td>
    </tr>
)

// eslint-disable-next-line
{/* implementation as class react component */ }
export default class ExercisesList extends Component {
    constructor(props) {
        super(props);

        this.deleteExercise = this.deleteExercise.bind(this)

        this.state = { exercises: [] };
    }


    componentDidMount() {
        // eslint-disable-next-line
        {/* get list of exercises from database */ }

        axios.get(`${baseUrl}/exercises`)
            .then(response => {
                this.setState({ exercises: response.data });
            })
            .catch((error) => {
                console.log(error);
            });
    }

    deleteExercise(id) {
        axios.delete(`${baseUrl}/exercises/` + id)
            .then(res => console.log(res.data));

        this.setState({
            exercises: this.state.exercises.filter(el => el._id !== id)
        })
    }


    exerciseList() {
        return this.state.exercises.map(currentexercise => {
            return <Exercise exercise={currentexercise} deleteExercise={this.deleteExercise} key={currentexercise._id} />;
        })
    }

    render() {
        return (
            <div>
                <h3>Logged Exercises</h3>
                <table className="table">
                    <thead className="thead-ligh">
                        <tr>
                            <th>Username</th>
                            <th>Description</th>
                            <th>Duration</th>
                            <th>Date</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.exerciseList()}
                    </tbody>
                </table>
            </div>
        )
    }
}